.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}


main {
  padding-top: 50px;
  background-color: #282c34;
  color: whitesmoke;
  align-items: center;
  min-height: calc(100vh - 50px);
}

.blogs {
  border: 1px solid whitesmoke;
}

aside {
  float: right;
  margin-right: auto;
  border: 1px solid whitesmoke;
  width: calc(100vw - 80%);
  height: calc(100vh - 40%);
  overflow: scroll;
}

#foot {
  text-align: center;
  background-color: rgb(33, 37, 41);
  color: whitesmoke;
  position: static;
  height: 50px;

}